import React, { useState } from 'react';
import './apr.css';

type IPeriod = 'Year' | 'Month' | 'Week' | 'Day';

const APRComponent = () => {
  const [selectedPeriod, setSelectedPeriod] = useState<IPeriod>('Year');
  const available: IPeriod[] = ['Year', 'Month', 'Week', 'Day'];

  return (
    <div className={'material-primary'}>
      <p className={'title'}>Expected Interest: 500 MAHA</p>
      <div className={'APR'}>
        {available.map((value, index) => {
          return (
            <div
              key={index}
              className={`apr-${index} ${value === selectedPeriod ? 'active-btn' : ''}`}
              onClick={() => setSelectedPeriod(value)}
            >
              {value}
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default APRComponent;
