import React from 'react';
import './index.css';
import { NavLink } from 'react-router-dom';
import navigation from '../menu/navigation';
import { useIntl } from 'react-intl';
import { useProtocolDataContext } from '../../libs/protocol-data-provider';

const Sidebar = () => {
  const intl = useIntl();
  const { currentMarketData } = useProtocolDataContext();

  return (
    <div className={'SidebarContainer'} id={'sidebar_container'}>
      <div className={'TopSideBar'}>
        {navigation.map((data, index) => {
          if (data.isVisible && !data.isVisible(currentMarketData)) {
            return <div key={index} />;
          } else {
            return (
              <NavLink
                key={index}
                className={'StyledLink'}
                exact
                activeClassName="activeSidebar"
                to={data.link}
              >
                {intl.formatMessage(data.title)}
              </NavLink>
            );
          }
        })}
      </div>
      <div className={'BottomSideBar'}>
        <a
          className={'StyledLinkHref'}
          href="https://docs.mahalend.com/"
          target={'_blank'}
          rel="noreferrer"
        >
          <div className={'single-line-center-start'}>
            <p>Documentation</p>
            <div />
          </div>
        </a>
      </div>
    </div>
  );
};

export default Sidebar;
