import React from 'react';
import { gradient, useThemeContext } from '@aave/aave-ui-kit';
import theme from '../../../theme';

interface GradientLineProps {
  height?: number;
}

export default function GradientLine({ height }: GradientLineProps) {
  const { currentTheme } = useThemeContext();

  const gradientBackground = theme.color.gradients.orange_gradient;

  return (
    <span className="GradientLine">
      <style jsx={true}>{`
        .GradientLine {
          width: 100%;
          height: ${height || 1}px;
          display: block;
          background: ${gradientBackground};
        }
      `}</style>
    </span>
  );
}
